7rem}#hero-section .hero-container .register-btn{font-size:1rem}#hero-section .hero-container .mentor-btn{font-size:0.7rem}}@media screen and (min-width: 600px) and (max-width: 800px){#hero-section .hero-container h1{font-size:10rem}#hero-section .hero-container .register-btn{font-size:1.5rem}#hero-section .hero-container .mentor-btn{font-size:1rem}}.temp-video{width:100%;max-height:40rem;opacity:0.15;z-index:-1}.temp-video .planning{display:block;margin:0 auto 0 auto;max-height:40rem}.vertical-timeline-element-date{color:black}.vertical-timeline-element-content{border-radius:7px}.dark-timeline .vertical-timeline-element-date{color:white}

